import sunuiStar from '../../../components/sunui-star/sunui-star.vue';
let i = 0;
import { mapState, mapGetters } from 'vuex';
export default {
	data() {
		return {
			taskCount: '',
			tiemCount: '',
			starCount: '',
			my_info: {},
			role:true,
		};
	},
	onLoad() {
		this.init();
		// this.numDH();
		console.log(this.user_info);
	},
	onShow() {
		console.log('show');
	},
	watch: {
		refresh_num(val) {
			console.log('my_refresh_num', val);
		}
	},
	components: {
		sunuiStar
	},
	computed: {
		...mapState(['user_info', 'refresh_num','isHead_res','isHead','is_login']),
		...mapGetters(['id']),
		avatarUrl() {
			return `url(${this.user_info.avatarUrl})`;
		},
		my_info_nav() {
			if (this.id) {
				return '/pages/tabbar/my/myInfo/myInfo';
			} else {
				return '/pages/tabbar/my/myInfo/createInfo';
			}
		},
		can_change_role(){
			return this.isHead_res == '1'
		}
	},
	methods: {
		init() {
			this.role = this.isHead == '0'?false:true
			if(this.is_login){
				this.get_user_info();
			}
			
		},
		numDH() {
			if (i < 20) {
				setTimeout(() => {
					this.taskCount = i;
					this.tiemCount = i;
					this.starCount = i;
					i++;
					this.numDH();
				}, 20);
			} else {
				this.taskCount = this.coutNum(1000);
				this.tiemCount = this.coutNum(2000);
				this.starCount = this.coutNum(300);
			}
		},
		coutNum(e) {
			if (e > 1000 && e < 10000) {
				e = (e / 1000).toFixed(1) + 'k';
			}
			if (e > 10000) {
				e = (e / 10000).toFixed(1) + 'W';
			}
			return e;
		},
		login_out() {
			uni.clearStorage();
			this.init()
			this.$store.commit('set_is_login',false)
			uni.navigateTo({
				url: '/pages/welcome/welcome'
			});
		},
		get_user_info() {
			this.$http
				.get('personwx.myinfodetail/1.0/', {
					id: this.id
				})
				.then(res => {
					console.log(res);
					this.my_info = this.$_.get(res,'data.data.personinfo',{})
				});
		},
		go_login(){
			uni.navigateTo({
				url: '/pages/welcome/welcome'
			});
		},
		change_role(e){
			this.role = e.detail.value
			this.$store.commit('setIsHead',this.role?'1':'0')
			uni.$emit('refreshJwt')
			this.init()
		}
	}
};
